import { createEl, scrollByX } from "./functions";///<---Ð£ Ð¼ÐµÐ½Ñ Ð½Ðµ Ð¿Ð¾Ð»ÑƒÑ‡Ð¸Ð»Ð¾ÑÑŒ Ð¸Ð¼Ð¿Ð¾Ñ€Ñ‚Ð¸Ñ€Ð¾Ð²Ð°Ñ‚ÑŒ ÑÑŽÐ´Ð° ÐºÐ°Ñ€Ñ‚Ð¸Ð½ÐºÐ¸, 
const main = document.querySelector("main");      ///Ð¼Ð¾Ð¶ÐµÑˆÑŒ ÑÐ°Ð¼ Ð¿Ð¾Ð¿Ñ€Ð¾Ð±Ð¾Ð²Ð°Ñ‚ÑŒ
class Swiper {
  slidesList = [];
  swiper = createEl("div", {
    className: "swiper",
  });
  constructor(parentNode) {
    parentNode.prepend(this.swiper);
  }
  addSlide(options = {}) {
    this.slide = createEl("div", {
      className: "slide",
    });
    this.image = createEl("image", {
      className: "image-bg",
    });
    this.arrowNext = createEl("div", {
      className: "arrow arrowNext",
      textContent: "âž¤",
    });
    this.arrowPrev = createEl("div", {
      className: "arrow arrowPrev",
      textContent: "âž¤",
    });
    this.info = createEl("div", {
      className: "info",
    });
    this.price = createEl("div", {
      className: "price",
      textContent: options.price,
    });
    this.description = createEl("div", {
      className: "description",
      textContent: options.description,
    });
    this.info.append(this.price, this.description);
    this.slide.append(this.arrowPrev, this.arrowNext, this.image, this.info);
    this.swiper.append(this.slide);
    this.slidesList.push({
      slide: this.slide,
      arrowNext: this.arrowNext,
      arrowPrev: this.arrowPrev,
      price: this.price,
      description: this.description,
    });
  }
  page = 0;
  addEvents() {
    this.slidesList.forEach((slide) => {
      slide.arrowNext.addEventListener("click", () => {
        this.autoScroll();
        this.page++;
        if (this.page > this.slidesList.length - 1) this.page = 0;
        // console.log( window.scrollX + this.slidesList[this.page].slide.getBoundingClientRect().left);<---Ð¿Ð¾ÑÐ¼Ð¾Ñ‚Ñ€Ð¸, ÐºÐ°Ðº Ð¸Ð·Ð¼ÐµÐ½ÑÐµÑ‚ÑÑ Ð°Ð±ÑÐ¾Ð»ÑŽÑ‚Ð½Ð¾Ðµ Ð·Ð½Ð°Ñ‡ÐµÐ½Ð¸Ðµ Ð¿Ð¾ Ð¥ Ð´Ð»Ñ Ñ‚Ñ€ÐµÑ‚ÑŒÐµÐ³Ð¾ ÑÐ»Ð°Ð¹Ð´Ð°, Ð² Ñ‡ÐµÐ¼ Ð¿Ñ€Ð¾Ð±Ð»ÐµÐ¼Ð°?
        scrollByX(this.swiper, this.slidesList[this.page].slide);// Ð¯ Ñ‡Ð°Ñ ÑÐ¸Ð´ÐµÐ» Ð½Ðµ Ð¼Ð¾Ð³ Ð¿Ð¾Ð½ÑÑ‚ÑŒ Ð¿Ð¾Ñ‡ÐµÐ¼Ñƒ ÑÑ‚Ð¾ Ñ‚Ð°Ðº Ñ€Ð°Ð±Ð¾Ñ‚Ð°ÐµÑ‚, Ð¿Ð¾ÐºÐ° Ð½Ðµ Ð´Ð¾Ð±Ð°Ð²Ð¸Ð» ÐºÐ¾Ð¾Ñ€Ð´Ð¸Ð½Ð°Ñ‚Ñƒ Ñ‡ÐµÑ€ÐµÐ· offsetLeft
      });
      slide.arrowPrev.addEventListener("click", () => {
        clearInterval(this.idInterval);
        this.page--;
        if (this.page < 0) this.page = this.slidesList.length - 1;
        scrollByX(this.swiper, this.slidesList[this.page].slide);
      });
    });
  }
  autoScroll() {
    this.idInterval = setInterval(() => {
      this.page++;
      if (this.page > this.slidesList.length - 1) this.page = 0;
      scrollByX(this.swiper, this.slidesList[this.page].slide);
    }, 3000);
  }
}
const swiper = new Swiper(main);
swiper.addSlide();
swiper.addSlide();
swiper.addSlide();
swiper.addEvents();
swiper.autoScroll();
window.addEventListener('mouseover', (event)=>{
  if(event.target.className === 'slide'){
    event.target.style.opacity = 0.8;
  }
  else{
    document.querySelectorAll('.slide').forEach(slide=>{
      slide.style.opacity = 1;
    })
  }
})
export { Swiper };
